import {Modal} from 'view-design'
import {myTypeof} from './functionGroup.js'


/**
 * config
 * @param {string/html} content:iViewcontent,
 * @param {number} height:,130,130
 * @param {number} width:416,416
 * @param {string/html} title:
 * @param {callback} onOk:
 * @param {boolean} onOkPromise:promise
 * @param {string/html} okText:
 * @param {string/html} cancelText:
 * @param {boolean} noWarnIcon:()false
 * @param {string} footerAlign:stringcenter
 * @param {boolean} cancelBt:booleantrue
 * this.messageBox({
 *                      content:''
 *                    })
 */
export default function messageBox({height, width = 416, title, content, onOk, onOkPromise, okText, cancelText, noWarnIcon, footerAlign, cancelBt}) {
  let heightTemp = height && Number(height) - 90 > 100 ? Number(height) - 90 + 'px' : 0
  let heightT = heightTemp || '100px'
  let stringContent = myTypeof(content) === 'String'
  Modal.warning({
    width: width,
    render: (h) => {
      return h('div', {
        class: 'customMessageBox',
        style: {
          height: heightT
        }
      }, [
        h('div', {
          class: 'containerN'
        }, [
          h('div', {
            class: 'titleN'
          }, [
            h('span', title || ''),
            h('Button', {
              class: 'fr closeN',
              props: {
                type: 'text'
              },
              on: {
                click() {
                  Modal.remove()
                }
              }
            }, [
              h('Icon', {
                props: {
                  type: 'ios-close',
                  size: 30
                }
              })
            ])
          ]),
          h('div', {
            class: 'contentN',
            style: {
              textAlign: stringContent ? 'center' : 'left'
            }
          }, [
            h('Icon', {
              props: {
                type: 'ios-alert-outline',
                size: 60,
                color: '#f8bb86'
              },
              class: stringContent && (noWarnIcon !== true) ? '' : 'hide'
            }),
            h('div', {class:'msgBoxConO'},content)
          ]),
          h('div', {
            class: 'footerN',
            style: {
              textAlign: footerAlign || 'center'
            }
          }, [
            h('Button', {
              class: 'okBtN',
              props: {},
              on: {
                click() {
                  if (myTypeof(onOk) === 'Function') {
                    if (onOkPromise) {
                      onOk()
                        .then(() => {
                          Modal.remove()
                        })
                        .catch(() => {
                          Modal.remove()
                        })
                    }
                    else {
                      onOk()
                      Modal.remove()
                    }
                  }
                  else {
                    Modal.remove()
                  }
                }
              }
            }, okText || ''),
            h('Button', {
              class: [
                'cancelBtN',
                cancelBt === false && 'hide'
              ],
              props: {},
              on: {
                click() {
                  Modal.remove()
                }
              }
            }, cancelText || '')
          ])
        ])
      ])
    }
  })
}
