import $swal from '../windowMethods/swal.js'

export default function (title, content, icon, onOk) {
  $swal({
    title: title || '',
    content: content || '',
    icon: icon || 'warning',
    buttons: ['']
  })
    .then(r => {
      if (r) {
        onOk()
      }
    })
}