<!--bg bg = '#fff'  bg = 'white'  (bg) <show-hide-panel bg="#fff"></show-hide-panel>  -->

<template>
  <div :style="getBg">
    <div v-show="show" class="contentX">
      <slot></slot>
    </div>
    <div class="arrowA">
      <div class="btA" @click="show=!show">
        <Icon :type="show?'ios-arrow-up':'ios-arrow-down'" size="20"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "showHidePanel",
    props: [
      'bg'
    ],
    data() {
      return {
        show: true
      }
    },
    computed: {
      getBg() {
        if (this.show) {
          return 'background:' + this.bg || 'transparent'
        } else {
          return ''
        }
      }
    }
  }
</script>